import * as React from "react"

import Layout from "../components/layout"
import Seo from "../components/seo"
import { Box, Heading, Text } from "@chakra-ui/react"
import { useContext, useEffect } from "react"
import { GlobalDispatchContext } from "../context/globalContextProvider"
import { graphql } from "gatsby"

const NotFoundPage = ({ data, location }) => {
  const dispatch = useContext(GlobalDispatchContext)

  useEffect(() => {
    dispatch({ type: "UPDATE_SELECTED_API_VERSION", payload: location })
    dispatch({
      type: "UPDATE_CURRENT_LOCATION",
      payload: { location: location },
    })
  }, [location])

  return (
    <Layout tocContents={data.rootToc} location={location}>
      <Box
        alignContent="flex-start"
        p={{ base: 4, md: 8, lg: 10 }}
        mt={{ base: 8, md: 10, lg: 12 }}
      >
        <Box>
          <Heading as="h1" size="xl" color="rlBlue">
            {" "}
            Not Found
          </Heading>
        </Box>
        <Box>
          <Text fontSize="lg">
            Sorry, but you are looking for something that isn&#39;t here.
          </Text>
        </Box>
      </Box>
    </Layout>
  )
}

export const Head = () => <Seo title="404: Not Found" />

export const query = graphql`
  query ($rootTocRegex: String!) {
    rootToc: markdownRemark(fileAbsolutePath: { regex: $rootTocRegex }) {
      fileAbsolutePath
      frontmatter {
        title
      }
      htmlAst
    }
  }
`
export default NotFoundPage
